													<div class="item-rest" v-if="storeItem.is_rest == 1">休息中</div>
													<img v-lazy="{src: storeItem.logo, loading: diydata.config.lazyload_store}">
													<div class="store-label" v-if="storeItem.label > 0">
														<div class="label" :style="{'background-color': storeItem.label_color}">{{storeItem.label_cn}}</div>
													</div>
												</router-link>
											</div>
											<div class="content-right">
												<router-link :to="util.getUrl({path:'/pages/store/goods', query:{sid: storeItem.id}})">
													<div class=""item-name-wrap>
														<div class="item-name" :style="{'color': diydata.diy.stores_list.diyitems.style.titlecolor}">{{storeItem.title}}</div>
													</div>
													<div class="item-score-time">
														<div class="item-score-sale">
															<div class="item-star-box">
																<van-rate v-model="storeItem.score" :size="12" :disabled-color="diydata.diy.stores_list.diyitems.style.scorecolor" disabled/>
															</div>
															<div class="item-sale">月售 {{storeItem.sailed}}</div>
														</div>
														<div class="time-distance">
															<div class="avg_delivery_time">{{storeItem.delivery_time}}分钟</div>
															<div class="line">|</div>
															<div class="avg_distance">{{storeItem.distance}}km</div>
														</div>
													</div>
													<div class="item-min-delivery">
														<div class="item-min-delivery-left">
															<div>起送价￥{{storeItem.send_price}}</div>
															<div class="line">|</div>
															<div>配送费￥{{storeItem.delivery_price}}</div>
														</div>
														<div class="item-min-delivery-right" :style="{'background-color': diydata.diy.stores_list.diyitems.style.deliverytitlebgcolor, 'color': diyitem.style.deliverytitlecolor}" v-if="storeItem.delivery_mode == 2">{{storeItem.delivery_title}}</div>
													</div>
												</router-link>
												<div class="discount-box" @click="onToggleDiscount(storeIndex, diyindex)" v-if="storeItem.activity && storeItem.activity.num > 0">
													<div class="toggle">
														<div class="icon " :class="{'icon-fold': storeItem.activity.is_show_all, 'icon-unfold': !storeItem.activity.is_show_all}" v-if="storeItem.activity && storeItem.activity.num > 2"></div>
													</div>
													<template v-for="(activityItem, index) in storeItem.activity.items" v-if="index < 2 || (storeItem.activity.is_show_all && index >= 2)">
														<div class="single-line">
															<img class="discount-icon" :src="'static/img/' + activityItem.type + '_b.png'">
															<div class="discount-text">{{activityItem.title}}</div>
														</div>
													</template>
												</div>
												<!--	热卖商品 -->
												<div class="hot-box" v-if="storeItem.hot_goods_num > 0 && (diydata.diy.stores_list.diyitems.params.showhotgoods == 1 || (diydata.diy.stores_list.diyitems.params.showhotgoods == 2 && diydata.diy.stores_list.diyitems.params.hotgoodsnum > storeIndex))" >
													<template v-for="item of storeItem.hot_goods">
														<router-link :to="util.getUrl({path:'/pages/store/goodsDetail', query:{sid: storeItem.id, id: item.id}})">
															<div class="hot-box-item">
																<div class="hot-t border-1px">
																	<img v-lazy="{src: item.thumb, loading: diydata.config.lazyload_goods}">
																	<div class="discount">{{item.discount}}折</div>
																</div>
																<div class="hot-b">
																	<div class="shop-name">{{item.title}}</div>
																	<div class="shop-price">
																		<div class="now-price">¥{{item.price}}</div>
																		<div class="old-price">¥{{item.old_price}}</div>
																	</div>
																</div>
															</div>
														</router-link>
													</template>
												</div>
											</div>
										</div>
									</div>
								</div>
							</van-list>
							<div class="loaded" v-if="diydata.stores.loaded">
								<div class="loaded-tips">我是有底线的</div>
							</div>
						</template>
						<template v-else>
							<div class="geolocationfail">
								<img src="static/img/store_no_con.png" alt="">
								<p>附近没有发现门店,我们正在努力覆盖中</p>
								<router-link :to="util.getUrl({path: '/pages/home/location'})" class="btn-location">切换地址</router-link>
							</div>
						</template>
					</template>
					<template v-else>
						<div class="geolocationfail">
							<img src="static/img/store_no_con.png" alt="">
							<p>获取定位失败!您可以选择手动搜索地址</p>
							<router-link :to="util.getUrl({path: '/pages/home/location'})" class="btn-location">手动搜索地址</router-link>
						</div>
					</template>
				</div>
			</div>
			<!-- 弹幕组 -->
			<template v-else-if="diyitem.id == 'danmu'">
				<transition name="danmu-fade" mode="out-in">
					<div class="order-danmu" :class="{'style2': diyitem.params.styleType, 'opacity-0': !danmu.show}" :style="{opacity: diyitem.style.opacity,top: diyitem.style.top + 'px', left: diyitem.style.left + 'px', background: diyitem.style.background}">
						<div class="flex">
							<img class="thumb" :src="danmu.item.avatar" alt="">
							<span class="info">新订单来自 {{danmu.item.nickname}}</span>
							<span class="time">{{danmu.item.time}}</span>
						</div>
					</div>
				</transition>

			</template>
		</template>
		<transition name="loading">
			<iloading v-if="preLoading"></iloading>
		</transition>
		<div class="geolocationfail" v-if="0">
			<img src="static/img/store_no_con.png" alt="">
			<p>获取定位失败!您可以选择手动搜索地址</p>
			<router-link :to="util.getUrl({path: '/pages/home/location'})" class="btn-location">手动搜索地址</router-link>
		</div>
	</div>
</template>
<script>
	import {mapState} from 'vuex'
	export default {
		props: {
			getLocationFail: {
				type: Boolean || Number
			},
			preLoading: {
				type: Boolean || Number
			},
			diydata: {
				type: Object,
			},
			showFixedSearchBar: false
		},
		computed: {
			...mapState([
				'locationInfo'
			]),
		},
		data() {
			return {
				lazyload_goods: '',
				lazyload_store: '',
				danmu: {
					show: false,
					index: 0,
					item: {
					}
				}
			}
		},
		components: {
		},

		methods: {
			onGetStore(force = false) {
				this.$emit('onGetStore', force);
			},
			onStoreOrderby(type, value, title) {
				this.$emit('onStoreOrderby', type, value, title);
			},
			onChangeStoreExtra(key) {
				this.$emit('onChangeStoreExtra', key);
			},
			onToggleDiscount(key, diyindex) {
				this.$emit('onToggleDiscount', key, diyindex);
			},
			onCloseRedpacket() {
				this.$emit('onCloseRedpacket');
			},
			onToggleDanmu() {
				var that = this;
				var danmu = that.diydata.diy.danmu;
				that.danmu.item = danmu[that.danmu.index];
				that.danmu.show = true;
				if(that.danmu.index == danmu.length - 1) {
					that.danmu.index = 0;
				} else {
					that.danmu.index++;
				}
				setTimeout(function(){
					that.danmu.show = false;
				}, 2500);
			}
		},
		activated() {
		},
		mounted() {
			var that = this;
			setTimeout(function() {
				if(that.diydata.diy.danmu) {
					that.onToggleDanmu();
					setInterval(function() {
						that.onToggleDanmu();
					}, 5000);
				}
			}, 5000);
		},
	}
</script>
<style scoped>
	@import "../../static/css/diy.css";
	::-webkit-scrollbar {/*滚动条整体样式*/
		width: 0px;   /*高宽分别对应横竖滚动条的尺寸*/
		height: 0px;
	}
	#diy .diy-picture{
		width: 100%;
		height: 150px;
		box-sizing: border-box;
	}
	#diy .diy-picture .van-swipe{
		width: 100%;
		height: 100%;
		box-sizing: border-box;
	}
	#diy .diy-picture .van-swipe img{
		width: 100%;
		height: 100%;
	}
	#diy .popup-search{
		box-sizing: border-box;
		width: 100%;
	}
	#diy .popup-search .button-list{
		padding: 15px;
	}
	#diy .popup-search .button-list .item{
		font-size:14px;
		display:inline-block;
		width:30%;
		text-align:center;
		border:1px solid #F1EFF0;
		height:30px;
		line-height:30px;
		border-radius:30px;
		margin-right:15px;
		color: #000;
	}
	#diy .popup-search .button-list .item.active{
		background-color:#FEFBF1;
		color:#FFD160;
		border-color:#FFD160;
	}
	#diy .popup-search .button-list .item .icon {
		font-size:17px;
		margin-right:5px;
		color:#FFD160;
	}
	#diy .popup-search .scroll{
		width: 100%;
		max-height: 350px;
		overflow-y: auto;
		margin-bottom: -1px;
	}
	#diy .popup-search .list-title{
		font-size:14px;
		background-color:#FBFBFB;
		position:relative;
		height:35px;
		line-height:35px;
		padding-left:15px;
	}
	#diy .popup-search .list-title .line{
		height:13px;
		width:3px;
		background-color:#FFD160;
		position:absolute;
		top:50%;
		left:0;
		transform:translate(0,-50%);
	}
	#diy .popup-search .active-list-item img{
		width: 17px;
		height: 17px;
		margin-right: 10px;
	}
	#diy .popup-search .active-list-item .icon{
		margin-right: 5px;
	}
	#diy .popup-search  .bottom-tab{
		height: 50px;
	}
	#diy .popup-search  .bottom-tab .clear{
		padding-left: 15px;
		font-size:12px;
		color:#848484;
	}
	#diy .popup-search  .finish{
		text-align:center;
		background-color:#FFD160;
		font-size:14px;
		width:100px;
		height: 100%;
		line-height: 50px;
		color: #000;
	}
	#diy .geolocationfail{
		margin: 20px 0 40px 0;
		width: 100%;
		height: 200px;
		text-align: center;
		color: #768491;
		font-size: 16px;
	}
	#diy .geolocationfail img{
		max-width: 25%;
		vertical-align: middle;
	}
	#diy .geolocationfail p{
		margin: 20px 0;
	}
	#diy .geolocationfail .btn-location{
		padding: 7px 16px;
		background: #ff2b4d;
		color: #fff;
		border-radius: 3px;
	}

	/* 公告 */
	#diy .diy-notice{
		background-color: #ffffff;
		height: auto;
		font-size: 12px;
		overflow: hidden;
	}
	#diy .diy-notice .diy-notice-container{
		background-color: #fff;
		padding: 10px 8px;
		overflow: hidden;
		display:flex;
		flex-flow: row nowrap;
		align-items: center;
		width:100%;
		box-sizing: border-box;
	}
	#diy .diy-notice .image {
		width: 15%;
		height: 16px;
		padding-right: 4px;
	}
	#diy .diy-notice .image img {
		height: 100%;
		width: 100%;
	}
	#diy .diy-notice .icon {
		height: 20px;
		padding-left: 4px;
		line-height: 22px;
		font-size: 16px;
	}
	#diy .diy-notice .icon.icon-notification{
		color: #fd5454;
	}
	#diy .diy-notice .notice-text {
		color: rgb(102, 102, 102);
		width: 75%;
		height: 20px;
		padding-left: 4px;
		font-size: 14px;
		line-height: 20px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	#diy .diy-notice .notice-text div {
		max-height:100%;
		height: 20px;
		max-width:100%;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}

	/*页面滚动一定距离后出现的置顶搜索框*/
	#diy .fiexd-searchbar{
		width: 100%;
		padding: 10px;
		background-color: #ff2d4b;
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		z-index: 9999;
	}
	#diy .fiexd-searchbar a{
		display: block;
		width: 100%;
		height: 32px;
		background-color: #fff;
		border-radius: 3px;
		font-size: 13px;
		color: #656565;
		text-align: center;
		line-height: 32px;
	}
	#diy .fiexd-searchbar a .icon{
		font-size: 16px;
		color: #666;
	}
</style>


