<template>
	<div id="public-header">
		<van-nav-bar :title="title" left-arrow @click-left="onClickLeft" @click-right="onClickRight" :style="{background: bgcolor, color: textcolor }">
			<div slot="left"><van-icon name="left" :style="{color: textcolor}"></van-icon></div>
		</van-nav-bar>
	</div>
</template>

<script>
	export default {
		name: 'PublicHeader',
		props:{
			title: String,
			bgcolor: String,
			textcolor: String
		},
		methods: {
			onClickLeft() {
				this.$router.go(-1);
			},
			onClickRight() {
				alert('按钮');
			}
		}
	}
</script>

<style>
	#public-header{
		position: fixed;
		top: 0;
		right: 0;
		left: 0;
		z-index: 100;
	}
	#public-header>div{
		background-color: #ff2d4b;
		color: #fff;
		font-size: 16px;
	}
	#public-header .van-icon{
		font-size: 24px;
		font-weight: bold;
		margin-right: 2px;
		color: #fff;
	}
	#public-header .van-hairline--bottom::after {
		border-bottom-width: 0px;
	}
</style>



